import styled from "styled-components";
import { Body, Description, SubTitle } from "../../styles/styles";
import { VariantsItem } from "../../animation/Variants";
import Effect3D from "./Effect3D";

const Card = ({ image, description, link, title }) => {
  return (
    <VariantsItem>
      <Effect3D>
        <ContainerCard>
          <img src={image} alt={title} />
          <Body>
            <SubTitle>{title}</SubTitle>
            <Description>{description}</Description>
            <Link href={link} target="_blank">
              Ver mas
            </Link>
          </Body>
        </ContainerCard>
      </Effect3D>
    </VariantsItem>
  );
};

const ContainerCard = styled.div`
  color: #fff;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  margin: 1.5em 0;
  cursor: pointer;

  &:hover ${Body} {
    opacity: 1;
  }

  ${Description} {
    color: #c1c1c1;
  }
`;

const Link = styled.a`
  background-color: blue;
  color: #fff;
  padding: 0.5em 1em;
  border-radius: 20px 0 20px 0;
`;

export default Card;
