import { Swiper as SwiperContainer } from "swiper/react/swiper";
import { VariantsContainer } from '../../animation/Variants';
import SwiperCore, { Autoplay } from "swiper";
SwiperCore.use([Autoplay]);

export const Swiper = ({ children }) => {
  return (
    <VariantsContainer>
      <SwiperContainer
        effect={"coverflow"}
        grabCursor={true}
        centeredSlides={true}
        slidesPerView={"auto"}
        spaceBetween={40}
        loop={true}
        coverflowEffect={{
          rotate: 0,
        }}
        autoplay={{
          delay: 4000,
        }}
      >
        {children}
      </SwiperContainer>
    </VariantsContainer>
  );
};
