import styled, { keyframes } from "styled-components";
import background from "../data/general/home.jpg";

export const updown = keyframes`
    0%{transform: translateX(-2em); opacity: 0;}
    100%{transform: translateX(0); opacity: 1;}
`;

export const Animate = styled.div`
    animation-name: ${updown};
    animation-duration: .5s;
`;

export const Title = styled.h3`
    text-align: center;
    margin-bottom: 0;
    font-size: 1.5em;
    animation-name: ${updown};
    animation-duration: .5s;
`;

export const SubTitle = styled.h3`
    text-align: center;
    font-size: 1em;
    font-weight: bold;
    margin-bottom: 0;
`;

export const Description = styled.p`
    text-align: center;
    color: gray;
    font-size: 1em;
`;

export const Icon = styled.i`
    color: blue;
    font-size: 1.5em;
    margin-left: ${props => props.left && '.5em'};
    margin-right: ${props => props.rigth && '.5em'};
`;

export const IconButton = styled.i`
    margin-left: ${props => props.left && '.5em'};
    margin-right: ${props => props.rigth && '.5em'};
`;

export const Button = styled.button`
    display: block;
    color: #fff;
    font-weight: bold;
    padding: .8em 1em;
    border-radius: 20px 0 20px 0;
    border: 2px solid blue;
    background-color: #141414;
    transition: .3s ease;
    cursor: pointer;
  
    &.active {
        background-color: blue;
    }
`;

export const Container = styled.div`
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1em;
`;

export const Nav = styled.nav`
    display: flex;
    gap: 1em;
    justify-content: center;
    margin-bottom: 1em;
`;

export const Card = styled.div`
    display: grid;
    grid-template-columns: 1fr max-content 1fr;
    column-gap: 1.5rem;
    text-align: center;
`;

export const Redondel = styled.span`
    margin-top: 3em;
    display: inline-block;
    width: 13px;
    height: 13px;
    background: blue;
    border-radius: 50%;
`;

export const Line = styled.span`
    display: block;
    width: 2px;
    height: 100%;
    background: blue;
    transform: translate(6px, -7px);
`;

export const Body = styled.div`
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: center;
    width: 100%;
    padding: 1em 0;
    background-color: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(20px);
    transition: .3s;
    opacity: 0;
`;

const blend = keyframes`
    0%{background-color: rgba(0,0,0,.9)}
    100%{background-color: rgba(0,0,0,.6)}
`;

export const Background = styled.div`
  background-image: url(${background});
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: soft-light;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  min-height: 100vh;
  animation-name: ${blend};
  animation-duration: 1s;
`;