import { useState } from 'react';
import skills from '../../data/json/skills.json';
import { Description, Title, Button, IconButton, Nav } from '../../styles/styles';
import Category from './Category';

const Skills = () => {
  const [category, setCategory] = useState('frontend')
  const frontend = () => setCategory('frontend')
  const backend = () => setCategory('backend')
  return (
    <div>
      <Title>Skills</Title>
      <Description>My technical level</Description>
      <Nav>
        <Button className={category === 'frontend' && 'active'} onClick={frontend}>
          <IconButton rigth className="fas fa-code" />
          Frontend
          <IconButton left className={category === 'frontend' ? 'fas fa-chevron-down' : 'fas fa-chevron-up'} />
        </Button>
        <Button className={category === 'backend' && 'active'} onClick={backend}>
          <IconButton rigth className="fas fa-database" />
          Backend
          <IconButton left className={category === 'backend' ? 'fas fa-chevron-down' : 'fas fa-chevron-up'} />
        </Button>
      </Nav>
      <Category category={category} skills={skills} />
    </div>
  )
}

export default Skills;