import { motion } from 'framer-motion/dist/framer-motion';

const containerVariants = {
    hidden: {
        opacity: 0,
        x: '-10vw'
    },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            type: 'spring',
            stiffness: 100
        }
    }
}

const Spring = ({children}) => {
    return (
        <motion.div variants={containerVariants} initial="hidden" animate="visible">
            {children}
        </motion.div>
    )
}

export default Spring
