import { useState } from 'react';
import { Button, Description, IconButton, Nav, Title } from '../../styles/styles';
import Courses from './Courses';
import Technique from './Technique';

const Education = () => {
    const [education, setEducation] = useState('technique')
    const technique = () => setEducation('technique')
    const courses = () => setEducation('courses')
    return (
        <div>
            <Title>Education</Title>
            <Description>My personal education</Description>
            <Nav>
                <Button className={education === 'technique' && 'active'} onClick={technique}>
                    <IconButton rigth className="fas fa-user-graduate" />
                    Technique
                    <IconButton left className={education === 'technique' ? 'fas fa-chevron-down' : 'fas fa-chevron-up'} />
                </Button>
                <Button className={education === 'courses' && 'active'} onClick={courses}>
                    <IconButton rigth className="fas fa-book" />
                    Courses
                    <IconButton left className={education === 'courses' ? 'fas fa-chevron-down' : 'fas fa-chevron-up'} />
                </Button>
            </Nav>
            {
                education === 'technique' && <Technique />
            }
            {
                education === 'courses' && <Courses />
            }
        </div>
    )
}

export default Education;
