import { VariantsContainer } from '../../animation/Variants';
import Skill from './Skill';

const Category = ({category, skills}) => {    
    return (
        <VariantsContainer>
            {
                skills.map(skill => (
                    skill.category === category &&
                    <Skill key={skill.id} {...skill} />
                ))
            }
        </VariantsContainer>
    )
}

export default Category
